.PlaylistList {
  text-align: center;
}

.PlaylistList-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.PlaylistList-items .card {
  padding: 1rem;
  text-align: center;
  background-color: black;
}

.PlaylistList-items .card:hover {
  cursor: pointer;
  box-shadow: 0 2px 10px 5px #1DB954;

}