@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;700&display=swap);
.Header {
  display: inline-block;
}

.Header:hover {
  cursor: pointer;
}

.Header-you {
  color: red;
  font-family: 'Raleway';
}

.Header-tify {
  color: #1DB954;
  font-family: 'Raleway';
  font-weight: bold;
}


.PlaylistList {
  text-align: center;
}

.PlaylistList-items {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.PlaylistList-items .card {
  padding: 1rem;
  text-align: center;
  background-color: black;
}

.PlaylistList-items .card:hover {
  cursor: pointer;
  box-shadow: 0 2px 10px 5px #1DB954;

}
.form-group > .form-control {
  background-color: black;

}

.form-group > .form-control:focus {
  background-color: black;
  color: white
}
.Dashboard-tabs {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.Dashboard-tabs-tab {
  padding: .5rem;
  margin: 1rem 1rem;
  border-radius: 7%;
}

.Dashboard-tabs-tab:hover {
  cursor: pointer;
  background-color: #1f1f22;
  border: solid 2px #778298;
}
.video-player {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.video-player iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-player-title {
  text-decoration: underline;
  font-weight: bold;
  margin-bottom: 2rem;
  color: #1DB954;
}
.Tracks-playlist-title {
  display: inline-block;
}

.Tracks-track {
  /* border: red solid 1px; */
  float: left;
  clear:left
}

.Tracks-track:hover {
  /* border: red solid 1px; */
  text-decoration: underline;
  cursor: pointer;
}

#startBtn {
  margin-bottom: 1rem;
  border-radius: 500px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: #2a2a2b;
  font-family: 'Montserrat', -apple-system, system-ui, BlinkMacSystemFont,
    'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  font-size: 20px;
  letter-spacing: 1px;
  color: #778298;
}

.btn {
  letter-spacing: 1px;
}

.login {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.login .btn {
  font-size: 1.2rem;
}

.main {
  width: 90%;
  margin: 2rem auto;
}

.form-control {
  font-size: 1.2rem;
}

.main-heading {
  margin: 2rem 0;
}

.hide {
  display: none;
}

.active {
  border: 2px solid #566273;
}

.loading {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-weight: bold;
  letter-spacing: 1px;
  margin-top: 10%;
  font-size: 150%;
}

.load-more {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 2rem 0;
}

.load-more button {
  border: none;
  color: #fff;
  border-radius: 10px;
  padding: 10px;
  letter-spacing: 1px;
  outline: none;
}

#loader {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 99999;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.4),
    rgba(0, 0, 0, 0.4)
  );
}

.message {
  position: relative;
  font-size: 150%;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
  color: #fff;
  letter-spacing: 1px;
}

.loader-open {
  overflow: hidden;
}

.errorMsg {
  color: #f21e08;
  background: #fff0f0;
  padding: 0.5rem;
  margin-top: 0.5rem;
}

.search-buttons {
  margin: 2rem 0;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.search-buttons .btn {
  margin: 0.8rem;
  font-size: 1.2rem;
  letter-spacing: 1px;
  font-weight: bold;
  color: #778298;
}

.search-buttons .btn:focus {
  box-shadow: none;
}

.playlist {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.card {
  margin: 1rem;
  box-shadow: 0 2px 7px 3px #949ca5;
}


.card img {
  max-width: 250px;
  height: 250px;
}

@media screen and (max-width: 400px) {
  .search-buttons {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .search-buttons .btn {
    margin: 0.6rem;
  }
  .card img {
    height: auto;
  }
  .main-heading {
    text-align: center;
  }
}
