.video-player {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
}

.video-player iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-player-title {
  text-decoration: underline;
  font-weight: bold;
  margin-bottom: 2rem;
  color: #1DB954;
}